* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
:root {
  --white: #fff;
  --Gainsborough: #dedede;
  --silver_1: #c1c1c1;
  --silver_2: #c3c3c3;
  --dull-grey: #707070;
  --disabled-grey: rgba(94,94,94,0.5);
  --wet-asphalt_1: #4a4a4a;
  --wet-asphalt_2: #484848;
  --grey-brown_1: #3f3f3f;
  --grey-brown_2: #343434;
  --BondiBeachWaters: #03a3d7;
  --LightGreyBlue: #8bc4c1;
  --DarkBlue: #0093dd;
  --DeepBlue: #23164d;
  --red: #f00;
}
html.block {
  overflow: scroll;
}
body {
  background-color: var(--white);
  font-size: 1rem;
  font-weight: 400;
  line-height: normal;
  color: var(--grey-brown);
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  overflow-x: hidden;
  box-shadow: 0px 0px 0.5rem 0px rgba(0,0,0,0.75);
  height: 100%;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}
.AllContent {
  flex: 1;
  width: 100%;
}
.AllContent .page_content90rem .container {
  max-width: 90rem;
}
#result {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
section {
  margin-bottom: 3rem;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: var(--grey-brown);
  font-size: 1rem;
  position: relative;
}
a.sogl {
  font-size: 14px;
  text-align: center;
  margin: 1rem 0;
  color: #808080;
  cursor: pointer;
  text-decoration: underline;
}
a.sogl:hover {
  color: #000;
}
a.back {
  width: auto !important;
  height: 2rem !important;
  padding: 0 1rem;
  border: 0.125rem solid #c4c4c4;
  border-radius: 0.25rem;
  margin-bottom: 0.75rem;
}
a.online:before {
  content: 'online';
  position: absolute;
  font-weight: bold;
  top: 0.5rem;
  right: -0.25rem;
  font-size: 0.625rem;
  letter-spacing: 0.3px;
  background: linear-gradient(135deg, #88c4c1 0%, #0093dd 50%, #23164d 100%);
  line-height: 1;
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  color: var(--white);
}
a.online:hover:before {
  border: 1px solid var(--dull-grey);
  color: var(--dull-grey);
  background: linear-gradient(135deg, #88c4c1 0%, #0093dd 50%, #23164d 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
a:hover {
  opacity: 0.8;
}
b,
.title {
  font-weight: 700;
}
h1 {
  font-size: 2.25rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
h4 {
  font-size: 1.5rem;
}
h5 {
  font-size: 1.25rem;
}
h6 {
  font-size: 1.125rem;
}
p {
