}
ul,
ol {
  list-style: none;
}
.container {
  max-width: 100rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 0.5rem;
}
.title {
  text-align: center;
  margin: 1.5rem auto;
  display: block;
  color: var(--grey-brown_1);
}
.burger {
  height: 2rem;
  width: 2rem;
  border-radius: 9px;
  display: none;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}
.burger.active {
  transform: rotate(45deg);
}
.burger.active svg > :nth-child(4) {
  opacity: 0;
}
.burger.active svg rect {
  fill: url("#burclose");
}
.burger:hover {
  opacity: 0.75;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}
.burger svg {
  width: 100%;
  height: 100%;
}
